.Burger {
  width: 60%;
  margin: auto;
  height: 250px;
  text-align: center;
  overflow: auto;
  font-weight: bold;
  font-size: 1.2rem;
}

.ingre {
  color: black;
}

@media (min-width: 1000px) and (min-height: 600px) {
  .Burger {
    width: 700px;
    height: 600px;
  }
}

@media (min-width: 1366px) and (min-height: 200px) {
  .Burger {
    width: 300px;
    height: 300px;
    padding-top: 50px;
  }
}

@media (min-width: 500px) and (min-height: 400px) {
  .Burger {
    width: 350px;
    height: 300px;
  }
}
@media (min-width: 500px) and (min-height: 401px) {
  .Burger {
    width: 412px;
    height: 400px;
  }
}
