.Toolbar {
  height: 56px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #703b09;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
  z-index: 90;
}

.Toolbar nav {
  height: 100%;
}

.Logo {
  height: 80%;
  display: flex;
  width: 60%;
  justify-content: space-between;
  padding-top: 20px;
}

.appName {
  margin-left: 25px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: chocolate;
  font-size: large;
}

@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
  .appName {
    display: none;
  }
  .Logo {
    width: auto;
  }
}
